import React from 'react';

const EventTimeInput = ({ time, changeHandler }) => {
  return (
    <div className="form-group">
      <label className=" w-100">
        Time:
        <input
          type="time"
          value={time}
          onChange={changeHandler}
          className="form-control"
        />
      </label>
    </div>
  );
};

export default EventTimeInput;



