import React from 'react';
import EventNameInput from './EventNameInput';
import EventDateInput from './EventDateInput';
import EventTimeInput from './EventTimeInput';
import EventDurationInput from './EventDurationInput';

const EventForm = ({
  name,
  date,
  time,
  duration,
  nameChangeHandler,
  dateChangeHandler,
  timeChangeHandler,
  durationChangeHandler,
  submitHandler
}) => {
  return (
    <form onSubmit={submitHandler} className="my-3 p-4 bg-dark text-white">
      <EventNameInput name={name} changeHandler={nameChangeHandler} />
      <div className="row">
        <div className="col">
          <EventDateInput date={date} changeHandler={dateChangeHandler} />
        </div>
        <div className="col">
          <EventTimeInput time={time} changeHandler={timeChangeHandler} />
        </div>
        <div className="col">
          <EventDurationInput
            duration={duration}
            changeHandler={durationChangeHandler}
          />
        </div>
      </div>

      <button className="btn btn-primary w-100" type="submit">
        Create new event
      </button>
    </form>
  );
};
export default EventForm;



