import React from 'react';

const EventDateInput = ({ date, changeHandler }) => {
  return (
    <div className="form-group">
      <label className=" w-100">
        Date:
        <input
          type="date"
          value={date}
          onChange={changeHandler}
          className="form-control"
        />
      </label>
    </div>
  );
};

export default EventDateInput;



