import React, { useState } from 'react';
import EventNameInput from './EventNameInput';

const Event = ({ event, nameUpdateHandler }) => {
  const [isModifying, setModifying] = useState(false);

  const createStatic = event => (
    <React.Fragment>
      <h2 className="card-title">{event.name}</h2>
      <p>{event.date}</p>
      <p>Duration (hours): {event.durationInHours}</p>
    </React.Fragment>
  );

  const createModifier = event => (
    <React.Fragment>
      <EventNameInput name={event.name} changeHandler={nameUpdateHandler} />
    </React.Fragment>
  );
  return (
    <li className="card my-4 shadow">
      <div className="card-body">
        {isModifying ? createModifier(event) : createStatic(event)}

        <button
          onClick={() => setModifying(!isModifying)}
          className="btn btn-dark mr-1"
        >
          {isModifying ? 'done' : 'modify'}
        </button>
        <button className="btn btn-dark ml-1">Delete</button>
      </div>
    </li>
  );
};

export default Event;



