import React from 'react';

const EventDurationInput = ({ duration, changeHandler }) => {
  return (
    <div className="form-group">
      <label>
        Duration:
        <input
          className="form-control"
          type="number"
          value={duration}
          min="1"
          max="10"
          onChange={changeHandler}
        />
      </label>
    </div>
  );
};

export default EventDurationInput;



